ks-product-price {
    display: flex;
    flex-direction: column;
    margin: 12px 0 20px;

    font-family: var(--product-price-font);
    font-weight: 700;
}

ks-product-price .main {
    display: flex;
    align-items: center;
    margin-bottom: 3px;
    width: fit-content;
}

ks-product-price .secondary {
    display: block;
    font-weight: 500;
    width: fit-content;
}

ks-product-price .secondary .price {
    font-size: var(--product-price-size);
    text-wrap: nowrap;
}

ks-product-price .current {
    color: var(--color-secondary);
    font-size: var(--product-price-size-emphasis);
    line-height: var(--product-price-size);
}

@media only screen and (max-width: 960px) {
    ks-product-price {
        align-items: center;
    }
}


@media only screen and (max-width: 560px) {
    ks-product-price .secondary {
        font-size: 15px;
        text-align: center;
    }

    ks-product-price .secondary .price {
        font-size: 16px;
    }
}

ks-product-price .currency {
    width: 26px;
    height: 26px;
    margin: 0 0 0 10px;
    border-radius: 30px;
    
    background-color: var(--color-secondary);
    color: white;

    font-family: var(--product-price-font);
    user-select: none;

    display: flex;
    justify-content: center;
    align-items: baseline;
}