import { Component, h, Host } from '@stencil/core';
import { common } from '../../../global/data/common';
import { product } from "../../../global/data/product";
import { priceFormatNoCurrency } from '../../../global/functions/price';

@Component({
	tag: 'ks-product-price',
	styleUrl: 'product-price.css'
})
export class ProductPrice {

	render() {
        const strings = product.get("strings");
        const symbol = common.get('currency').symbol;
        const currentPrice = priceFormatNoCurrency(product.get("currentPrice"));
        const previousPrice = priceFormatNoCurrency(product.get("previousPrice"));
        const lowestPrice = priceFormatNoCurrency(product.get("lowestPrice"));

		return <Host>
            <div class="main" aria-label={`Cena produktu - ${currentPrice} ${symbol}`}>
                <div class="current">{currentPrice}</div>
                <div class="currency">{symbol}</div>
            </div>

            {(lowestPrice && previousPrice) && <div class="secondary">
                Sugerowana cena producenta: <s class="price">{previousPrice} {symbol}</s>
            </div>}

            {lowestPrice && <div class="secondary">
                {strings.lowestPrice} <s class="price">{lowestPrice} {symbol}</s>
            </div>}
        </Host>;
	}
}
